body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #fdfdfd;
}

.container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.app-header {
  background-color: #2ecc71;
  padding: 2.5rem 3rem;   /* bigger vertical padding */
  min-height: 100px;      /* taller header */
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  color: white;
  font-size: 3rem;        /* bigger text */
  font-weight: bold;
  text-decoration: none;
  cursor: default;        /* not clickable */
}

.nav-buttons {
  display: flex;
  gap: 1rem;
}

.nav-button {
  background-color: white;
  color: #2ecc71;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-weight: bold;
  text-decoration: none;
  transition: background-color 0.3s ease;
}

.nav-button:hover {
  background-color: #ecf0f1;
}

.main {
  text-align: center;
  padding: 3rem 1rem;
}

.title {
  color: #2ecc71;
  font-size: 2rem;
  margin-bottom: 2rem;
}

.content-box h2 {
  font-weight: bold;
  margin-bottom: 1rem;
}

.content-box p {
  margin-bottom: 0.5rem;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.action-buttons {
  margin-top: 1rem;
}

.btn {
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 20px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.btn.green {
  background-color: #2ecc71;
  color: white;
  margin: 0 0.5rem;
}

.btn.green:hover {
  background-color: #27ae60;
}

.btn.login,
.btn.register {
  background-color: white;
  color: #2ecc71;
  border: 2px solid white;
  transition: 0.3s;
}

.btn.login:hover,
.btn.register:hover {
  background-color: #ecf0f1;
}
